<template>
  <div class="layOut">
    <navbar :token="token" :activeItem="this.activeItem" />
    <SideCar :token="token" class="sideCar" />
    <App-main class="main" ref="main"/>
    <layout-footer v-if="Footer"  class="footer" />
  </div>
</template>

<script>
import { Navbar, AppMain, SideCar, layoutFooter } from "./components";
import { getToken } from "@/utils/auth";
import { mapGetters } from "vuex";
export default {
  name: "Layout",
  components: { Navbar, AppMain, SideCar, layoutFooter },
  provide() {
    return {
      tabBarData: [
        { name: "第九艺术首页", key: "Home" }
        // { name: "赠品", key: "Reward" },
        // { name: "福利资讯", key: "Gift" },
        // { name: "评测", key: "Subject" },
        // { name: "折扣", key: "Discount" },
        // { name: "热门", key: "Hot" }
      ]
    };
  },
  data() {
    return {
      token: getToken(),
      activeItem: ""
    };
  },
  watch: {
    $route(newval) {
      const name = this.$route.name;
      this.activeItem = (this.key).includes(name) ? name : "";
      this.token = getToken();
      // console.log(document.body.scrollTop);
    }
  },
  computed: {
    ...mapGetters("common", ["hasFooter"]),
    Footer() {
      // if (this.hasFooter.includes(true)) {
      //   return true;
      // }
      // TODO 页面页脚问题待解决
      return true;
    },
    key() {
      return this._provided.tabBarData.map(res => res.key);
    }
  }
};
</script>
<style lang="less" scoped>
  .layOut {
    background: rgb(15, 13, 26);
    min-height: 100vh;
    position: relative;
    .Navbar {
      position: fixed;
      top: 0;
      z-index: 999;
    }
    .main {
      padding-bottom: 200px;
    }
    .sideCar {
      position: fixed;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      z-index: 999;
    }
    .footer {
      position: absolute;
      bottom: 0;
    }
  }
</style>
